<script>
export default {
  name: 'DayCounterView',
  data() {
    return {
      selectedDate: '1949-10-01 00:00:00', // é»˜è®¤æ–°ä¸­å›½æˆç«‹æ—¥
      selectedPreset: 'newChina',
      result: null,
      updateTimer: null,
      presetDates: [
        {
          key: 'newChina',
          label: 'ğŸ‡¨ğŸ‡³ æ–°ä¸­å›½æˆç«‹',
          date: '1949-10-01 00:00:00',
          description: 'ä¸­åäººæ°‘å…±å’Œå›½æˆç«‹'
        },
        {
          key: 'reformOpening',
          label: 'ğŸŒŸ æ”¹é©å¼€æ”¾',
          date: '1978-12-18 00:00:00',
          description: 'ä¸­å›½æ”¹é©å¼€æ”¾å¼€å§‹'
        },
        {
          key: 'hongkongReturn',
          label: 'ğŸ™ï¸ é¦™æ¸¯å›å½’',
          date: '1997-07-01 00:00:00',
          description: 'é¦™æ¸¯å›å½’ç¥–å›½'
        },
        {
          key: 'macaoReturn',
          label: 'ğŸ° æ¾³é—¨å›å½’',
          date: '1999-12-20 00:00:00',
          description: 'æ¾³é—¨å›å½’ç¥–å›½'
        },
        {
          key: 'olympics2008',
          label: 'ğŸ… åŒ—äº¬å¥¥è¿',
          date: '2008-08-08 20:00:00',
          description: 'åŒ—äº¬å¥¥è¿ä¼šå¼€å¹•'
        },
        {
          key: 'newYear2000',
          label: 'ğŸŠ åƒç¦§å¹´',
          date: '2000-01-01 00:00:00',
          description: '21ä¸–çºªå¼€å§‹'
        },
        {
          key: 'newYear2020',
          label: 'ğŸ† 2020å¹´',
          date: '2020-01-01 00:00:00',
          description: '2020å¹´ä»£å¼€å§‹'
        },
        {
          key: 'valentine',
          label: 'ğŸ’• æƒ…äººèŠ‚',
          date: '2024-02-14 00:00:00',
          description: '2024å¹´æƒ…äººèŠ‚'
        }
      ],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      milestones: []
    }
  },
  mounted() {
    this.calculateDifference();
    this.startTimer();
  },
  beforeDestroy() {
    this.stopTimer();
  },
  methods: {
    selectPreset(preset) {
      this.selectedDate = preset.date;
      this.selectedPreset = preset.key;
      this.calculateDifference();
    },
    
    calculateDifference() {
      if (!this.selectedDate) return;
      
      const startDate = new Date(this.selectedDate);
      const now = new Date();
      
      if (startDate > now) {
        this.$message.warning('é€‰æ‹©çš„æ—¥æœŸä¸èƒ½æ™šäºå½“å‰æ—¶é—´');
        return;
      }
      
      const diffMs = now.getTime() - startDate.getTime();
      
      // è®¡ç®—æ€»æ•°
      const totalSeconds = Math.floor(diffMs / 1000);
      const totalMinutes = Math.floor(totalSeconds / 60);
      const totalHours = Math.floor(totalMinutes / 60);
      const totalDays = Math.floor(totalHours / 24);
      
      // è®¡ç®—è¯¦ç»†åˆ†è§£
      const years = now.getFullYear() - startDate.getFullYear();
      let months = now.getMonth() - startDate.getMonth();
      let days = now.getDate() - startDate.getDate();
      let hours = now.getHours() - startDate.getHours();
      let minutes = now.getMinutes() - startDate.getMinutes();
      let seconds = now.getSeconds() - startDate.getSeconds();
      
      // å¤„ç†è´Ÿæ•°æƒ…å†µ
      if (seconds < 0) {
        seconds += 60;
        minutes--;
      }
      if (minutes < 0) {
        minutes += 60;
        hours--;
      }
      if (hours < 0) {
        hours += 24;
        days--;
      }
      if (days < 0) {
        const prevMonth = new Date(now.getFullYear(), now.getMonth(), 0);
        days += prevMonth.getDate();
        months--;
      }
      if (months < 0) {
        months += 12;
      }
      
      this.result = {
        totalDays: totalDays.toLocaleString(),
        totalHours: totalHours.toLocaleString(),
        totalMinutes: totalMinutes.toLocaleString(),
        totalSeconds: totalSeconds.toLocaleString(),
        years,
        months,
        days,
        hours,
        minutes,
        seconds
      };
      
      this.calculateMilestones(startDate, now);
    },
    
    calculateMilestones(startDate, now) {
      const milestones = [];
      const diffMs = now.getTime() - startDate.getTime();
      const totalDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));
      
      // å®šä¹‰é‡Œç¨‹ç¢‘
      const milestoneTargets = [
        { days: 100, title: 'ç™¾æ—¥çºªå¿µ', icon: 'ğŸ’¯' },
        { days: 365, title: 'ä¸€å‘¨å¹´', icon: 'ğŸ‚' },
        { days: 1000, title: 'åƒæ—¥çºªå¿µ', icon: 'ğŸŠ' },
        { days: 1825, title: 'äº”å‘¨å¹´', icon: 'ğŸ‰' },
        { days: 3650, title: 'åå‘¨å¹´', icon: 'ğŸ†' },
        { days: 7300, title: 'äºŒåå‘¨å¹´', icon: 'ğŸ’' },
        { days: 10950, title: 'ä¸‰åå‘¨å¹´', icon: 'ğŸ‘‘' }
      ];
      
      milestoneTargets.forEach(target => {
        const targetDate = new Date(startDate.getTime() + target.days * 24 * 60 * 60 * 1000);
        const reached = totalDays >= target.days;

        // æ£€æŸ¥æ—¥æœŸæ˜¯å¦æœ‰æ•ˆ
        let formattedDate = '';
        if (!isNaN(targetDate.getTime())) {
          try {
            formattedDate = this.formatDate(targetDate.toISOString().slice(0, 19).replace('T', ' '));
          } catch (error) {
            formattedDate = this.formatDate(targetDate.getFullYear() + '-' +
              String(targetDate.getMonth() + 1).padStart(2, '0') + '-' +
              String(targetDate.getDate()).padStart(2, '0') + ' 00:00:00');
          }
        }

        milestones.push({
          type: target.days,
          title: target.title,
          icon: target.icon,
          date: formattedDate,
          reached,
          remaining: reached ? '' : `è¿˜éœ€ ${target.days - totalDays} å¤©`
        });
      });
      
      this.milestones = milestones;
    },
    
    formatDate(dateStr) {
      if (!dateStr) return '';
      const date = new Date(dateStr);
      return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      });
    },
    
    startTimer() {
      this.updateTimer = setInterval(() => {
        this.calculateDifference();
      }, 1000);
    },
    
    stopTimer() {
      if (this.updateTimer) {
        clearInterval(this.updateTimer);
        this.updateTimer = null;
      }
    }
  }
}
</script>

